.gallery {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 100%;
    margin: 0 auto;
    gap: 2rem;
    background-color: #f05f40;
}

.project {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    padding: 3rem;
    margin: 0 auto;
    background-color: #f05f40;
}

#project {
    display: block;
    height: 40px;
    visibility: hidden;
}

@media screen and (max-width: 940px) {
    .project {
        gap: 2.8rem;
    }
}

@media screen and (max-width: 768px) {
    .project {
        gap: 1.2rem;
        padding: 1rem 0;
    }
}
