.home {
    background: rgba(0, 0, 0, 0.7);
    color: #fff;
    height: 100vh !important;
    width: 100%;
    position: relative;
}

.home:before {
    content: '';
    background: url('../assets/emile-perron-xrVDYZRGdw4-unsplash.jpg') no-repeat
        center center/cover;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}

.home .container {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 1100px;
    margin: auto;
    padding: 1rem;
}

.container p:first-child {
    font-style: italic;
    font-size: 1.2rem;
    animation: animTitle1 2s ease 0s 1 normal forwards;
}

.container p:nth-child(2) {
    font-size: 2.2rem;
    animation: animTitle2 2.3s ease 0s 1 normal forwards;
}

.container p:nth-child(3) {
    font-size: 5rem;
    font-weight: 700;
    animation: animTitle3 2.6s ease 0s 1 normal forwards;
}

.button {
    padding: 10px 28px;
    margin: 1rem 0;
    width: 300px;
    text-align: center;
    font-size: 1.2rem;
    font-weight: 600;
    text-transform: uppercase;
    background-color: #f05f40;
    border-radius: 5px;
    color: #333;
    transition: 0.4s ease-out;
    cursor: pointer;
    animation: animBtn 2.9s ease 0s 1 normal forwards;
}

.button:hover {
    background-color: #d05e40;
}

.shadow {
    text-shadow: 4px 4px 10px #f05f40;
}

@media screen and (max-width: 940px) {
    .container p:nth-child(3) {
        font-size: 2.6rem;
    }
}
